header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 4;
}

header .navbar {
  background-color: #96C5DC;
  padding: .6rem 0rem .6rem 1rem;
  height: 10vh;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

header .navbar__logo {
  width: 4%;
}

header .navbar__menu {
  width: 25%;
  position: absolute;
  right: -50px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

header .navbar__menu li {
  display: inline-block;
}

header .navbar__menu li a {
  font-weight: 700;
  text-transform: capitalize;
  padding-right: 2rem;
  line-height: 2px;
}

header .navbar__menu li .navbar__btn {
  height: 2rem;
  border: 1px solid #045e8b;
  border-radius: 10px;
  background-color: transparent;
  text-transform: capitalize;
  padding: 0 0 0 .4em;
  margin: auto 3%;
  width: 100%;
}

header .navbar__menu li .navbar__btn a {
  color: #045e8b;
}

header .navbar__menu li .navbar__btn a i {
  -webkit-transform: translateX(1em);
      -ms-transform: translateX(1em);
          transform: translateX(1em);
  color: #f4d01a;
  font-size: 20px;
}

header .navbar__menu li .navbar__link {
  color: black;
}

header .navbar__menu li .navbar__link--active {
  color: #045e8b;
}

.hero {
  background-color: #f4d01a;
  text-align: center;
  height: 100%;
  margin-top: 5.8%;
  padding: 1% auto;
}

.hero__info {
  font-size: 1em;
  color: black;
  text-transform: uppercase;
  -webkit-transform: translateY(7px);
      -ms-transform: translateY(7px);
          transform: translateY(7px);
}

.hero__description {
  width: 35%;
  margin: 0 auto;
  font-size: .8rem;
  font-weight: 700;
  color: #1078ad;
  line-height: 2;
}

.test {
  width: 95%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.test__img {
  height: 60vh;
}

.test__right {
  width: 100%;
  height: 60vh;
  text-align: center;
  margin: 1% -4% 0 3%;
}

.test__right h3 {
  width: 100%;
  font-size: 1.3rem;
  color: #045e8b;
  font-weight: 800;
}

.test__right img {
  margin-top: 1%;
}

.test__right p {
  line-height: 2;
  font-size: 1.3rem;
  font-weight: 700;
  color: gray;
}

.test__right button {
  width: 70%;
  height: 3rem;
  padding: 1% 0;
  background-color: #045e8b;
  border: none;
  border-radius: 5px;
}

.test__right button a {
  color: #fff;
  font-size: 1.4rem;
  font-weight: 900;
  letter-spacing: 1px;
}

.symptome {
  height: 100%;
  width: 60%;
  margin: 10% auto 0;
  text-align: center;
}

.symptome h1 {
  font-size: 1.8rem;
  font-weight: 900;
  font-style: bolder;
  color: #045e8b;
  margin: 5% auto;
}

.symptome .icon {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
}

.symptome .icon__box {
  width: 100%;
  height: 100%;
}

.symptome .icon__box img {
  max-width: 146px;
  max-height: 146px;
}

.symptome .icon__box p {
  font-size: 1.6rem;
  font-weight: 700;
  font-style: bolder;
  color: black;
}

.recommandation {
  background-color: #045e8b;
}

.recommandation__title {
  font-size: 1.7rem;
  font-weight: 700;
  padding-top: 1.5rem;
  text-align: center;
  color: #f3f2f2;
}

.recommandation .container {
  width: 90%;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.recommandation__conseil p {
  font-size: 1.1rem;
  font-weight: 900;
  color: #fff;
  margin-bottom: 5%;
  line-height: 1.5;
  letter-spacing: 2px;
}

.recommandation__conseil p span {
  color: black;
}

.recommandation__appel {
  text-align: center;
  margin: 2% 0 3% 25%;
  -webkit-transform: translateX(20px) translateY(-30px);
      -ms-transform: translateX(20px) translateY(-30px);
          transform: translateX(20px) translateY(-30px);
}

.recommandation__appel p {
  color: #f4d01a;
  font-size: 1.3rem;
  font-weight: 800;
  padding: 2%;
}

.recommandation__appel button {
  height: 3.5rem;
  background-color: #f4d01a;
  padding: .5rem 1rem;
  font-size: 1.4rem;
  font-weight: 900;
  color: #045e8b;
  margin: 3% auto;
  border-radius: 2px;
  border: none;
}

.recommandation__appel div {
  font-size: 1.2rem;
