  -webkit-text-decoration: underline #f4d01a !important;
          text-decoration: underline #f4d01a !important;
  font-size: 1.3rem;
  font-style: bolder;
  letter-spacing: 1px;
}

.demarage-test {
  background-color: #96C5DC;
  text-align: center;
  z-index: 9;
}

.demarage-test__txt {
  color: #1078ad;
  font-size: 1.5rem;
  font-weight: 800;
  padding-top: .7rem;
}

.demarage-test__img {
  padding: 1% 0 1%;
