}

.demarage-test__btn {
  width: 40%;
  background-color: #045e8b;
  border-radius: 2px;
  border: none;
  height: 3rem;
  margin-bottom: 1rem;
}

.demarage-test__btn a {
  font-size: 1.7rem;
  font-weight: 700;
  color: #fff;
  letter-spacing: 1px;
}

.demarage-test__btn:hover {
  background-color: #1078ad;
  -webkit-transition: ease-in 1s;
  -o-transition: ease-in 1s;
  transition: ease-in 1s;
}

footer {
  height: 50vh;
}

footer .logo {
  width: 60px;
  margin: 1% 45%;
}

footer .contenaur {
